@import url(https://fonts.googleapis.com/css2?family=Outfit:wght@500&family=Satisfy&display=swap);
*{
  margin: 0;
  padding: 0;
}

html{
  scroll-behavior: smooth;
}

body {
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

body{
  overflow-x: hidden;
}
.App {
  height: 100vh;
  text-align: center;
  font-family: 'Outfit', sans-serif;
}

:root{
  --hue-color:212;

  --color-principal:hsl(var(--hue-color),66%,5%);

  --bg-nav:hsl(var(--hue-color),66%,98%);

  --color-active:hsl(var(--hue-color),66%,20%);


  --bg-color: hsl(var(--hue-color),66%,98%);
  --color-border: hsl(var(--hue-color),66%,47%);
  --color-text:hsl(var(--hue-color),66%,100%);

  --color-title: hsl(var(--hue-color),66%,54%);


  --color-subtitle: hsl(var(--hue-color),41%,60%);
   

  --bg-nav: hsl(var(--hue-color),66%,97%);

  --bg-container: hsl(var(--hue-color),66%,98%);

  --color-hover : hsl(var(--hue-color),66%,100%);

  --bg-btn : #2225;

  --color-subtitles : hsl(var(--hue-color),72%,38%);

  
  --color-parrafos : hsl(var(--hue-color),72%,75%);

  --color-contents : rgb(170, 202, 238,.2) ;

  --color-secun : hsl(var(--hue-color),72%,75%);

  --strong-color : hsl(var(--hue-color),41%,19%)
}

body{
  --color-parrafos : hsl(var(--hue-color),72%,75%);
  --color-subtitles : hsl(var(--hue-color),72%,38%);
  --color-principal:hsl(var(--hue-color),66%,10%);
  --bg-container: hsl(var(--hue-color),66%,97%);
  --bg-btn : #2225;
  --color-links : #6f98bd;

}

body.dark{
  --color-parrafos : hsl(var(--hue-color),72%,75%);
  --color-subtitles : hsl(var(--hue-color),72%,38%);
  --color-principal:hsl(var(--hue-color),66%,47%);
  --bg-container: hsl(var(--hue-color),66%,10%);
  --color-active:hsl(var(--hue-color),66%,98%);
  --bg-nav: hsl(var(--hue-color),66%,15%);

  --bg-btn : #65befa;

}

.menu_icon{
  cursor: pointer;
  position: relative;
  z-index: 120;
}

.header{
  transition: .7s ;
}

.header.active{
  position: fixed;
  z-index: 1000;
  background-color: hsl(212,66%,20%);
  background-color: var(--color-active);
}

.header.active .title-name{
  color: hsl(212,72%,38%);
  color: var(--color-subtitles);
}
.header.active .menu-content{
  color: hsl(212,72%,38%);
  color: var(--color-subtitles);
}
.content_home{
  position: relative;
  display: flex;
  /* padding-top: 70px ; */
  justify-content: center;
  align-items: center; 
  flex-direction: column;
  /* background:url('img-home.jpeg');
  background-position: center;
  background-size: cover;
  background-attachment: fixed;
  background-color: #5558;
  background-blend-mode: hard-light;  */
  
}


.text-animation.AnimationText{
  transform: translateY(0);
  opacity: 1;
  text-align: center;
}




.img1{
  width: 100%;
  height: 100%;
  overflow: hidden;
}

.img2{
  position: absolute;
  width: 100%;
  height: 100%;
  top: 0;
  left: 0;
  -webkit-user-select: 0;
      -ms-user-select: 0;
          user-select: 0;
  mix-blend-mode: screen;
}


.content_items{
  width: 100%;
  height: 100%;
  display: flex;
  align-items: flex-start;
  flex-direction: column;
  list-style: none;
  opacity: 0;
  padding: 50px;
  visibility: hidden;
  transform: translateY(-100px);
  transition: 1.5s ease all;
}


.item_nav{
   position: relative;
   padding-bottom: 3px;
   margin-bottom: 20px;

}

.item_nav::before{
  content: '';
  position: absolute;
  left: 0;
  bottom: 0;
  width: 0;
  height: 3.5px;
  background-color:hsl(212,72%,38%);
  background-color:var(--color-subtitles);
  transition: 0.5s ease;
}

.item_nav.active::before{
  width: 100%;
}

.item_nav.active a{
   color: hsl(212,66%,20%);
   color: var(--color-active);
   font-size: 18px;
}
.item_nav_text{
   text-decoration: none;
   color:var(--color-links);
   font-size: 15px;
}


.content_nav.active{
   transform: translateX(0);
   display: flex;
   box-shadow : 2px 0 0px 800px #1115;

}

.content_nav.active .content_items{
  transform: translateY(0);
  opacity: 1;
  width: -webkit-fit-content;
  width: -moz-fit-content;
  width: fit-content;
  height: -webkit-fit-content;
  height: -moz-fit-content;
  height: fit-content;
  visibility: visible;
  transition-delay: .7s;
}

.content_home_text{
  width: 100%;
  height: -webkit-fit-content;
  height: -moz-fit-content;
  height: fit-content;
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
}

.description{
  color: #eee;
  font-size: 15px;
  padding: 10px 0;
  text-align: center;
}
.btns-info{
  margin: 10px 0;
}
.btns-info button{
     cursor: pointer;
     position: relative;
     z-index: 10;
     border-radius: 5px;
     background: #fff5;
     border: none;
     outline: none;
     color: #fff;
     padding: 8px;
     font-size: 12px;
     font-weight: 600;
     margin-right: 8px;
     overflow: hidden;
     transition: .7s ease all;
}
.btns-info button::before{
  content: '';
  position: absolute;
  z-index: -1;
  top: -10px;
  left: -15px;
  border-radius: 50px;
  width: 250%;
  height: 200%;
  transform: translateX(-100%);
  background-color: #fff;
  transition: .7s ease all;
}
.btns-info button:hover{
  color: #222;
}

.btns-info button:hover::before{
  transform: translateX(0%);
}

.content_social{
  list-style: none;
  padding: 10px 0;
  height: 200px;
  display: flex;
  justify-content: center;
  align-items: flex-start;
}

.item_social{
  margin: 0 15px;
  -webkit-filter: invert(1);
          filter: invert(1);
  width: 20px;
  cursor: pointer;
  height: 20px;
}

.figure-content{
  display: flex;
  align-items: center;
  margin-top:20px;
  position: relative;
  width: 205px;
  height: 190px;
}
.iconos{
  display: inline-block;
  width: 50px;
  color: hsl(212,41%,60%);
  color: var(--color-subtitle);
  height: 50px;
  animation: scaleAnimationIcon 1s alternate infinite ease-in-out;
}

@keyframes scaleAnimationIcon{

  100%{
     transform: scale(1.1);
  }

}

.icon-code{
  position: relative;
  top: -15px;
  color:#fff;
  font-size: 150px;
  padding: 5px;
  animation: scaleAnimation 1s alternate infinite ease-in-out;
}

@keyframes scaleAnimation {

    100%{
       transform: scale(1.1);
    }

}

.text-animation{
  transform: translateY(0);
}


