.content_tecnologia_info{
  position: relative;
  z-index: 100;
  width: 80%;
  height: 200px;
  display: flex;
  align-items: center;
  padding: 10px 0 ;
  margin:  30px auto;
  background-color:rgb(170, 202, 238,.2);
  background-color:var(--color-contents);
  border-radius: 5px;
  box-shadow: 0 0 5px 2px #2222;
  transition: .7s ease all;

}

.content_tecnologia_info::before{
  content: '';
  position: absolute;
  top: -0%;
  right: 0;
  width: 0%;
  height:100%;
  border-radius: 5;
  background: hsl(212,41%,19%);
  background: var(--strong-color);
  transition: 1.2s ease-in-out all;
  z-index: -1;
}
.content_tecnologia_info:hover{
  transform: scale(1.1);
  background-color:hsl(212,72%,75%);
  background-color:var(--color-secun);
}
.content_tecnologia_info:hover::before{
    width: 80%;
}

.content_tecnologia_info.css:hover::before{
    width: 50%;
}

.content_tecnologia_info.React:hover::before{
  width: 60%;
}

.content_tecnologia_info.js:hover::before{
  width: 65%;
}


